export default {
    props:{
        tabBars:Array,
        tabIndex:Number,
    },
    methods:{
        //tabBar点击事件
        tabSwitch(index){
            //点击操作获取了当前的index，再将其写入tabIndex中，匹配的项目改变样式
            this.$emit('tabSwitch', index);
        },
    }
}
